/// background-color: #000
.main_slider_item {
	background-color: #D8EBED;
	border-radius: 15px;
	padding: 30px;
	.main_slider_img {
		margin: 0 auto;
		width: auto; } }
.main_slider_content {
	margin-top: 30px; }
.slide_subtitle {
	font-weight: 500;
	font-size: 13px;
	@media(min-width:992px) {
		font-size: 16px; } }
.slide_description {
	margin: 15px 0 40px;
	font-size: 10px;
	@media(min-width:992px) {
		font-size: 12px; } }
.main_slider_left, .main_slider_right {
	position: absolute;
	top: 50%; }
.main_slider_left {
	transform: translate(-50%, -100%); }

.main_slider_right {
	transform: translate(50%, -100%);
	right: 0; }

.partners_slider, .our_blog_slider {
	.slider_item {
		height: 120px;
		img {
			margin: 0 auto; } }
	.owl-nav {
		position: absolute;
		top: -55px;
		right: 0px; }
	.owl-nav button.owl-prev, .owl-nav button.owl-next {
		height: 20px;
		outline: none;
		span {
			outline: none;
			font-size: 30px;
			margin-right: 10px; } } }
.slider_title {
	font-weight: 700;
	font-size: 18px;
	margin-bottom: 20px;
	padding-bottom: 10px;
	border-bottom: 1px solid #e4e4e4; }
.our_blog_slider {
	.slider_item {
		height: auto;
		img {
			margin: 0; } }
	.slider_item_title {
		margin: 10px 0; }
	.slider_item_text {
		// font-weight: 700
		font-size: 14px; }
	.our_blog_read_more {
		position: relative;
		width: 160px;
		font-size: 14px;
		font-weight: 700;
		margin: 10px 0;
		// padding: 10px 0
		display: block;
		&::after {
			position: absolute;
			content: '';
			right: 0px;
			top: 0px;
			width: 23px;
			height: 15px;
			background-image: url('../../img/icons/read_more_arr.png'); } } }
